<template>
    <div>
        <h1>about</h1>
        <img class="show" data-img="1.jpg">
        <p>{{aboutMsg}}</p>
        <div>
            <h2>图片放这里</h2>
            <img :src="bgImg">
        </div>
        <home-header></home-header>
    </div>
</template>
<script>
import HomeHeader from './pages/header1'
export default {
    data () {
        return {
            aboutMsg: '我是about组件',
            bgImg:require("../assets/logo.png")
        }
    },
    methods:{
        show(){
            var img=document.getElementsByClassName("show")[0];
            var bg=`${img.getAttribute("data-img")}`
            this.bgImg=require('../assets/'+bg)
            console.log(this.bgImg)
        }
    },
    mounted(){
        this.show();
    },
    components:{
        HomeHeader
    }
}
</script>


