<template>
    <div>
        <h1>home</h1>
        <p>{{msg}}</p>
        <div>
            <button v-for="(list,index) in lists" :key="index" @click="change(index)">home{{index}}</button>
        </div>
        <home-header1 v-show="lists.home1"></home-header1>
        <home-header2 v-show="lists.home2"></home-header2>
        <home-header3 v-show="lists.home3"></home-header3>
    </div>
</template>
<script>
import HomeHeader1 from './pages/header1'
import HomeHeader2 from './pages/header2'
import HomeHeader3 from './pages/header3'
export default {
    data () {
        return {
            msg: "我是home 组件",
            lists:{
                home1:true,
                home2:false,
                home3:false
            }
        }
    },
    methods:{
        change(index){
            for(var key in this.lists){
                this.lists[key]=false;
                this.lists[index]=true;
            }
        },
    },
    components:{
        HomeHeader1,
        HomeHeader2,
        HomeHeader3
    }
}
</script>


